<template>
  <div class="add-task-page">
    <div class="add-task-box">
      <!-- 性质 -->
      <div class="addtask-set-item-box">
        <p class="set-title">性质：</p>
        <p
          class="set-choice"
          v-for="item in typeList"
          :key="item.id"
          :class="{ select: taskType === item.id }"
          @click="taskType = item.id"
        >
          {{ item.text }}
        </p>
      </div>
      <!-- 等级 -->
      <div class="addtask-set-item-box">
        <p class="set-title">等级：</p>
        <div class="stars-box">
          <Stars
            class="stars"
            :isCLickActive="true"
            :num="oldaign"
            :size="20"
            @getStarsnum="getStarsnum"
          ></Stars>
        </div>
      </div>
      <!-- 状态 -->
      <div class="addtask-set-item-box">
        <p class="set-title">状态：</p>
        <span
          v-for="item in stateList"
          class="state iconfont"
          :class="[item.id, { select: stateVal === item.id }]"
          :key="item.id"
          @click="stateVal = item.id"
          >{{ item.icon }} {{ item.text }}</span
        >
      </div>
      <!-- 信息输入部分 -->
      <div class="addtask-set-item-box">
        <input
          type="text"
          placeholder="任务标题"
          class="todo-input"
          @keyup.enter="addTaskFn"
          v-model.trim="matterTitle"
        />

        <textarea
          v-model.trim="matterContent"
          class="my-note"
          placeholder="任务描述"
        ></textarea>
      </div>
      <div class="addtask-set-item-box">
        <p class="empty-input-btn" @click="emptyInput">清除输入</p>
        <p class="add-task-btn" @click="addTaskFn">添加任务</p>
        <p
          class="add-note-btn"
          title="选择笔记内容添加至任务管理"
          @click="openNoteFn"
        >
          添加笔记
        </p>
      </div>
    </div>
    <!-- 统计数据展示 -->
    <div class="statistics-box">
      <div class="statistics">
        <!-- 任务统计 -->
        <div class="count-box">
          <!-- 循环数量统计，key="item.num+item.name"数量改变   key="item"任何改变都从新渲染 -->
          <div
            class="count-box-item"
            v-for="item in taskListNum"
            :key="item.num + item.name"
          >
            <p class="count-title">{{ STATE_CH[item.name] }}</p>
            <div class="count-num">
              <h-numAnimation
                :eleNumber="item.num"
                :numColor="'#000'"
                :fontSize="38"
                :numSpeed="50"
              />
            </div>
          </div>
        </div>
        <div class="swh-swh">
          <div class="swh-a">待开发</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  reactive,
  toRefs,
  onBeforeMount,
  onMounted,
  getCurrentInstance,
  watch
} from 'vue';
import { useRoute } from 'vue-router';
import { stateList, typeList, STATE_CH } from './config.js';
export default {
  name: '',
  props: {
    taskListNum: {
      type: Array
    }
  },
  setup(props, ctx) {
    const route = useRoute();
    const { proxy } = getCurrentInstance();
    const data = reactive({
      STATE_CH,
      stateList, //状态数据
      typeList, //性质数据
      taskType: 'work', //任务性质
      oldaign: 1, //等级
      stateVal: 'notStarted', //状态 ,默认未开始
      matterTitle: '', //任务标题
      matterContent: '' //任务内容
    });
    watch(
      // 监听路由参数
      () => route.params,
      (val) => {
        if (val.content) {
          // 改变任务内容
          data.matterTitle = val.content.substring(0, 15);
          data.matterContent = val.content;
        }
      },
      { immediate: true } //立即执行一次
      // { deep: true }   深度监听
    );
    onBeforeMount(() => {});
    onMounted(() => {});
    // 获取等级
    const getStarsnum = (newval) => {
      data.oldaign = newval;
    };
    // 添加任务
    const addTaskFn = () => {
      if (data.matterTitle) {
        const matterObj = {
          //创建新的数据对象
          matter_id: Date.now().toString(16), //唯一id
          matter_date: new Date().toLocaleString(),
          matter_title: data.matterTitle, //标题
          matter_content: data.matterContent, //内容
          matter_grade: data.oldaign, //优先级
          matter_label: data.taskType, //标签
          matter_state: data.stateVal //状态
        };
        // console.log(matterObj);
        ctx.emit('addTask', matterObj); //增加任务
        emptyInput();
      } else {
        proxy.h_tips({
          type: 'error',
          msg: ' 请输入任务标题',
          top: 30,
          time: 2000
        });
      }
    };
    // 清空任务
    const emptyInput = () => {
      // 清空输入内容和标题
      data.matterContent = '';
      data.matterTitle = '';
    };
    // 打开笔记
    const openNoteFn = () => {
      // 触发home.vue中的事件
      proxy.$bus.emit('openNote');
    };
    return {
      emptyInput,
      openNoteFn,
      addTaskFn,
      getStarsnum,
      ...toRefs(data)
    };
  }
};
</script>
